<template>
    <div class="article">
        <div class="loading" v-if="isLoading">
            <img src="../assets/loading.gif">
        </div>

        <div v-else>
            <div class="pannel">
                <div class="title_wrap">
                    <h2>{{post.title}}</h2>
                    <ul>                
                        <li>发布于:{{post.create_at | formatDate }}</li>
                        <li>作者:{{post.author.loginname}}</li>
                        <li>{{post.visit_count}}次浏览</li>
                        <li>来自{{post | tabFormatmsg }}</li>
                    </ul>
                    <div v-html="post.content" class="inner_content"></div>
                </div>

                <div class="reply_wrap">
                    <h3>回复</h3>
                    <div v-for="(reply,index) in post.replies" class="reply_item" :key="reply.id">
                        <router-link :to="{
                            name:'user_info',
                            params:{
                                name: reply.author.loginname
                            }
                        }">
                            <img :src="reply.author.avatar_url" class="avatar">
                        </router-link>

                        <router-link :to="{
                            name:'user_info',
                            params:{
                                name: reply.author.loginname
                            }
                        }">
                            <span class="reply_author">{{reply.author.loginname}}</span>
                        </router-link>

                        <span class="reply_time">{{index+1}}楼•{{reply.create_at | formatDate}}</span>
                        <span v-if="reply.ups.length > 0 ">
                        ☝   {{reply.ups.length}}
                        </span>
                        <span v-else></span>
                        <div v-html="reply.content" class="reply_content"></div>
                </div>
                </div>

            </div>

        </div>
    </div>
</template>

<script>
export default {
    name: 'Article',
    data(){
        return{
            isLoading: false,
            post: {}
        }
    },
    methods:{
        getArticleDate(){
            this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
            .then(res =>{
                if(res.data.success == true){
                    this.isLoading = false
                    this.post = res.data.data
                }
            })
            .catch( err => {
                console.log(err)
            })
        }
    },
    beforeMount(){
        this.isLoading = true
        this.getArticleDate()
    },
    watch:{
        '$route'(to,from){
            this.getArticleDate()
        }
    }
}
</script>

<style>
@import url('../assets/markdown-github.css');

ul,li{
    list-style: none;
}
ul{
    padding: 0;
}
.article{
    margin-right: 325px;
    padding: 0;
}
.title_wrap,.reply_wrap{
    padding: 10px;
    background: #fff;
}
.title_wrap h2{
    padding-left:30px;
    font-size: 22px;
    color: #333;
    grid-column: 1;
    grid-row: 1;
}
.title_wrap ul{
    padding-left: 10px;
    font-size: 12px;
    color: #838383;
}
.title_wrap li{
    display: inline-block;
}
.title_wrap li:before{
    content: "•";
}
.inner_content{
    font-size: 15px;
}
a{
    color: #08c;
    text-decoration: none;
}

h3{
    font-size: 14px;
    font-weight: normal;
    padding: 10px;
    background: #f6f6f6;
    border-radius: 3px 3px 0 0;
}
.avatar{
    width: 30px;
    height: 30px;
    border-radius: 3px;
    vertical-align: middle;
}
.reply_author,.reply_time{
    font-size: 12px;
}
.reply_author{
    color: #666;
}
.reply_time{
    color: #08c;
}
.reply_content{
    font-size: 14px;
}
.reply_item{
    border-bottom: 1px solid #e1e1e1;
    padding: 10px;
}
</style>



