</template>

<script>
export default {
    name: "Header"
}
</script>

<style scoped>
.header{
    background: #444;
    font-size: 13px;
    padding: 5px;
}
.header:after{
    content: '';
    display: block;
    clear:both;
}
.header_wrap{
    width: 80%;
    margin: 0 auto;
}
