<template>
    <div class="Good">
        <div class="loading" v-if="isLoading">
            <img src="../assets/loading.gif">
        </div>

        <div class="posts">
            <ul>
                <li>
                    <div class="top_bar">
                        <a href="#">全部</a>
                        <a href="#">精华</a>
                        <a href="#">分享</a>
                        <a href="#">问答</a>
                        <a href="#">招骋</a>
                        <a href="#">客户端测试</a>        
                    </div>
                </li>

                <li v-for="post in posts">
                    <router-link :to="{
                            name:'user_info',
                            params:{
                                name: post.author.loginname
                            }
                        }">
                        <img :src="post.author.avatar_url" class="avatar"> 
                    </router-link>
                    <!-- 回复/浏览 -->
                    <span class="reply_count_all">
                        <span class="reply_count">{{post.reply_count}}</span>                
                            /
                        <span class="visit_count">{{post.visit_count}}</span>                        
                    </span>    

                     <!-- 帖子分类 -->
                    <h3 :class="[{put_good:(post.good == true),put_top:(post.top == true),put_tab:(post.good != true && post.top != true)}]">
                        {{ post | tabFormatmsg}}
                    </h3>

                    <!-- 标题 -->
                    <router-link :to="{
                        name: 'post_content',
                        params:{
                            id: post.id,
                            name: post.author.loginname
                        }
                    }">
                        <p>
                            {{post.title}}
                        </p>
                    </router-link>               
                    <!-- 最终回复时间 -->
                    <span class="last_active_time">
                        {{post.last_reply_at | formatDate}}
                    </span>               
                </li>

                <li>
                    <pagination @handleList = "renderList"></pagination>
                </li>
            </ul>
        </div>
    </div>
</template>


<script>
import pagination from './Pagination'
export default {
    name: 'Good',
    data(){
        return {
            isLoading: false,
            posts: [],
            postpage: 1
        }
    },
    components:{
        pagination
    },
    methods:{
        getData(){
            this.$http.get('https://cnodejs.org/api/v1/topics',{
               params:{
                page: this.postpage,
                limit: 20
               }
            })
            .then(res => {
                this.isLoading = false
                this.posts = res.data.data
            })
            .catch(err => {
                console.log(err)
            })
        },
        renderList(value){
            this.postpage = value
            this.getData()
        }
    },
    beforeMount(){
        this.isLoading = true
        this.getData()
    }
}
</script>

<style scoped>
    ul,li{
        list-style: none;
    }
    ul{
        padding: 0;
        margin: 0;
    }
    .top_bar{
        background-color: #f6f6f6;
        border-radius: 3px 3px 0 0;;
    }
    .top_bar a{
        display: inline-block;
        color: #80bd01;
        font-size: 14px;
        text-decoration: none;
        margin: 0 10px;
        padding: 10px;
    }

    li:not(:first-child){
        border-bottom: 1px solid #f0f0f0;
        padding: 0 10px;
        background: #fff;
        position: relative;
        display: grid;
        grid: auto auto/ 35px 75px 40px 2fr 60px ;
    }

    li:not(:first-child) a{
        color: #888;
        text-decoration: none;
    }

    li:not(:first-child) a:hover{
        text-decoration: underline;
    }

    li:not(:first-child):hover{
        background: #f5f5f5;
    }

    .avatar,.reply_count_all,h3,p,.last_active_time{
        grid-row: 1 / span 1;
        align-self: center;
    }

    .avatar{
        width:30px;
        height: 30px;
        border-radius:5px;
        padding: 10px 0;
        grid-column: 1;
    }

    .reply_count_all{
        font-size: 12px;
        text-align: center;
        grid-column: 2;
    }

    .reply_count{
        color: #9e78c0;
    }

    .visit_count{
        color: #b4b4b4;
    }

    h3{
        padding: 1px 5px;
        margin: 0 auto;      
        border-radius: 3px;        
        font-weight: normal;
        font-size: 12px;
        text-align: center;
        grid-column: 3;
    }

    h3.put_top,h3.put_good{
        background: #80bd01;  
        color: #fff;
    }

    h3.put_tab{
        background:#e5e5e5;
        color: #999;
    }

    p{
        padding-left:5px;
        font-size: 14px;
        grid-column: 4;
    }

    .last_active_time{
        font-size: 12px;
        color: #778087;
        grid-column: 6;
        text-align: right;
    }

    li:last-child{
        display: inherit;
        border:none;
    }
</style>



