<template>
    <div class="author_wrap">
        <div class="authorinfo">
            <div class="top_bar">作者</div>
            <router-link :to="{
                    name:'user_info',
                    params:{
                        name: userinfo.loginname
                    }
                }">
                    <img :src="userinfo.avatar_url" class="avatar">
            </router-link>
                <!-- <span class="name">{{userinfo.loginname}}</span>
                <span class="score">{{userinfo.score}}积分</span>                -->
          


        </div>

        <div class="recent_suject">
            <div class="top_bar">作者最近主题</div>
            <ul>
                <li v-for="list in topiclimitby5">
                    <router-link :to="{
                        main: 'post-content',
                        params:{
                            id: list.id,
                            name: list.author.loginname
                        }
                    }">
                    {{list.title}}
                    </router-link>
                </li>
            </ul>
        </div>

        <div class="recent_reply">
            <div class="top_bar">作者最近回复</div>
            <ul>
                <li v-for="list in replylimitby5">
                    <router-link :to="{
                        main: 'post-content',
                        params:{
                            id: list.id,
                            name: list.author.loginname
                        }
                    }">
                    {{list.title}}
                    </router-link>
                </li>
            </ul>            
        </div>
    </div>
</template>

<script>
export default {
    name: 'SlideBar',
    data(){
        return{
            userinfo: {}
        }
    },
    methods:{
        getData(){
            this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
            .then(res =>{           
                if(res.data.success == true){
                    this.isLoading = false
                    this.userinfo = res.data.data
                }
            })
            .catch( err => {
                console.log(err)
            })
        }       
    },
    computed:{
        topiclimitby5(){
            if(this.userinfo.recent_topics){
                return this.userinfo.recent_topics.slice(0,5)
            }else{
                return this.userinfo.recent_topics
            }
        },
        replylimitby5(){
            if(this.userinfo.recent_replies){
                return this.userinfo.recent_replies.slice(0,5)
            }else{
                return this.userinfo.recent_replies
            }
        }        
    },
    beforeMount(){
        this.isLoading = true
        this.getData()       
    }
}
</script>

<style>
.authorinfo,.recent_reply,.recent_suject{
    background: #fff;
    margin-bottom: 10px;
}

.author_wrap{
    width: 300px;
    float: right;
    margin-top: 0;
    font-size: 14px;
}

.author_wrap li{
    padding: 3px 0 ;
}
.author_wrap .recent_reply ul,.author_wrap .recent_suject ul {
    margin-top: 0px;
    margin-bottom: 0px;
    list-style: none;
    padding-left: 14px;
}

.author_wrap ul a {
    font-size: 12px;
    text-decoration: none;
    color: #778087;
}

.author_wrap .top_bar {   
    background: #f6f6f6;
    height: 16px;
    font-size: 12px;
    color: #51585c;
    border-radius: 3px 3px 0 0;
    margin-bottom: 10px;
    padding: 10px;
}

.author_wrap img {
    height: 48px;
    width: 48px;
    border-radius: 3px;
    margin: 10px;
}

.author_wrap .loginname {
    width: 100px;
    float: right;
    margin-top: 22px;
    margin-right: 159px;
    font-size: 14px;
}

.author_wrap .loginname a {
    text-decoration: none;
    color: #778087;
}

.author_wrap .top_bar {
    margin-top: 0px;
}



</style>



