// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'


Vue.prototype.$http = Axios
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('formatDate',function(str){
  if(!str) return ''
  var date = new Date(str) //格式化
  var time = new Date().getTime()-date.getTime() //现在时间-传入的时间 = 相差时间 (单位是毫秒)
   
    if (time/1000 < 30){ //除1000,是因为单位是毫秒
      return  '刚刚'
    }else if(time / 1000 < 60) {
      return  parseInt(time / 1000)+'秒前'
    }else if(time / 60000 < 60) {
      return parseInt(time / 60000)+'分钟前'
    }else if((time/ (1000*3600)) < 24){
      return parseInt(time/(1000*3600)) +'小时前'
    }else if((time/(1000*3600*24)) < 31){
      return parseInt(time/(1000*3600*24)) + '天前'
    }else if ((time /(1000*3600*24*30)) < 12) {
      return parseInt(time /(1000*3600*24*30)) + '月前'
    } else {
      return parseInt(time /(1000*3600*24*30*12)) + '年前'
    }
})

Vue.filter('tabFormatmsg',function(post){//处理显示模块的文字
  if(post.good === true){
    return '精华'
  }else if(post.top === true){
    return '置顶'
  }else if(post.tab == 'ask'){
    return '问答'
  }else if(post.tab == 'share'){
    return '分享'
  }
})


