<template>
    <div>
        <div class="loading" v-if="isLoading"> <!--在数据未返回的时侯，显示这个正在加载的动图-->   
            <img src="../assets/loading.gif">
        </div>

        <div  v-else>
            <div class="pannel">
                <router-link :to="{name:'root'}">
                    <h3>主页</h3>
                </router-link>

                <div class="userinfo_wrap">
                    <img :src="userinfo.avatar_url" class="avatar">
                    <span class="name">{{userinfo.loginname}}</span>
                    <span class="score">{{userinfo.score}}积分</span>
                    <span class="regtime">注册时间:{{userinfo.create_at | formatDate}}</span>
                </div>
            </div>

            <!-- 回复主题  -->
            <div class="replys">
                <h3>回复主题</h3>
                <ul>
                    <li v-for = "item in userinfo.recent_replies" :key="item.id">                
                    <router-link :to="{
                        name: 'post_content',
                        params:{
                            id: item.id
                        }
                    }">                
                            {{item.title}}                      
                    </router-link>

                    </li>
                </ul>


            </div>

            <!-- 创建主题  -->
            <div class="creates">
                <h3>创建主题</h3>
                <ul>
                    <li v-for = "item in userinfo.recent_topics" :key="item.id">
                   <!-- 图像 -->
                    <img :src="item.author.avatar_url" class="avatar">
          
                    <!-- 标题 -->
                    <router-link :to="{
                        name: 'post_content',
                        params:{
                            id: item.id
                        }
                    }">                
                        <p>{{item.title}} </p>                     
                    </router-link>
                    <!-- 最终回复时间 -->
                    <span class="last_active_time">
                        {{item.last_reply_at | formatDate}}
                    </span>
                    </li>
                </ul>


            </div>
        </div>

    </div>
</template>


<script>
export default {
    name: 'UserInfo',
    data(){
        return{
            isLoading: false,
            userinfo: {}
        }
    },
    methods:{
        getData(){
            this.$http.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
            .then(res =>{           
                if(res.data.success == true){
                    this.isLoading = false
                    this.userinfo = res.data.data
                }
            })
            .catch( err => {
                console.log(err)
            })
        }
    },
    beforeMount(){
        this.isLoading = true
        this.getData()
    }   
}
</script>


<style>
ul,li{
    list-style: none;
}
ul{
    padding: 0;
}
li{
    border-bottom: 1px solid #e1e1e1;   
}
a{
    text-decoration: none;
}
.replys li ,.creates li {
    padding: 10px;
}
.replys li a:visited,.replys li a,.creates li a:visited,.creates li a{
    color: #08c;
    text-decoration: none;
}
.replys li a:hover,.creates li a:hover{
    color: #20547c;
    text-decoration: underline;
}
.main li:last-child{
    border-bottom: none;
    padding: 10px;
}
.userinfo_wrap,.reply,.replys,.creates{
    background: #fff;
}

.userinfo_wrap{
    padding: 10px;
    display:grid;
    grid:auto auto auto auto / 50px 1fr;   
}
h3{
    font-size: 14px;
    font-weight: normal;
    padding: 10px;
    margin: 0;
    background: #f6f6f6;
    border-radius: 3px 3px 0 0;
    color: #80bd01;
}

.userinfo_wrap .avatar{
    width: 40px;
    height: 40px;
    margin-bottom: 10px;
    border-radius: 3px;
    vertical-align: middle;
    grid-column: 1 / span 2;
    grid-row: 2;
}
.userinfo_wrap .name{
    font-size: 12px;
    color: #778087;
    grid-column: 2;
    grid-row: 2;
}
.userinfo_wrap .score{
    font-size: 14px;
    grid-column: 1 / span 2;
    grid-row: 3;
}
.userinfo_wrap .regtime{
    font-size: 14px;
    color: #ababab;
    padding: 10px 0;
    grid-column: 1 / span 2;
    grid-row: 4;
}
.creates{
    position: relative;

}
.creates li{
    display: grid;
    grid: auto auto/ 35px 2fr 1fr ;
}
.creates .avatar, .creates .reply_count_all, .creates h3, .creates p, .creates .last_active_time{
    grid-row: 1 / span 1;
    align-self: center;
}
.creates .avatar{
    width:30px;
    height: 30px;
    border-radius:5px;
    grid-column: 1;
}
.creates p{
    grid-column: 2;
    padding-left: 20px;
    margin: 0;
}
.creates .last_active_time{
    font-size: 12px;
    color: #778087;
    grid-column: 6;
    text-align: right;
}
</style>



